import React from 'react'
import { createUseStyles } from 'react-jss'
import PropTypes from 'prop-types'

const useStyles = createUseStyles({
    list: {
        display: 'flex',
        flexFlow: 'column',
        width: '100%',
        height: 'fit-content'
    },
    row_root: {
        display: 'flex',
        width: '100%',
        height: 'fit-content'
    },
    row_top: {
        display: 'flex',
    },
    row_image: {
        width: '2rem',
        height: '2rem',
    },
    percentage_root: {
        height: '2rem',
        display: 'flex',
        alignItems: 'center'
    },
    percentage: {
        marginLeft: '1rem',
        textAlign: 'center',
    }
})



export default function BalanceList(props) {
    const classes = useStyles()

    const Row = props => {
        return (
            <div>
                <div className={classes.percentage_root}>
                    <div className={classes.row_image}/>
                    <div style={{ width: `${props.portfolio_percentage}%`, background: props.backgroundColor, height: '1.5rem' }}></div>
                    <div className={classes.percentage} ><span>{`${Math.round(props.portfolio_percentage, 2)}%`}</span></div>
                </div>
            </div>
        )
    }

    return (
        <div>
            {props.data && props.data.map(currency => <Row {...currency} />)}
        </div>
    )
}

BalanceList.propTypes = {
    data: PropTypes.arrayOf(
        PropTypes.shape({
            portfolio_percentage: PropTypes.number,
            value_usd: PropTypes.number,
            // value_btc: PropTypes.number,
            symbol: PropTypes.string,

        })
    )
}